import { extendObservable } from 'mobx';


class UserStore{
    constructor(){
        extendObservable(this,{
            loading:true,
            isLoggedIn:false,
            showlogin: false,
            showproducts:false,
            showcart:false,
            showcheckout:false,
            showPreview:false,
            previewimage:'',
            previewname:'',
            previewprize:'',
            username:''
        })
    }
}
export default new UserStore();