import React        from 'react'
import              '../Cartproduct/Cartproduct.css'
import {toast}      from 'react-toastify'
toast.configure()

class Cartproduct extends React.Component{
    constructor(props){
        super(props)
        
        this.state={
            productlist :'',
            showlist    :false,
            total       :0,
            quantity    :0
        }
        this.cart();
    }
    updateprize(prize,id){
        let x =document.getElementsByClassName(id);
        let y = x[0].value;

    
        // this.setState({
        //     total:total+prize*parseInt(x)
        // })
        document.getElementById(id).innerHTML=parseInt(y)*parseInt(prize);
    }

    async deleteproduct(id){
        try{
            let res =await fetch('/deleteproduct',{
              method:'post',
              headers:{
                'Accept':'application/json',
                'Content-Type': 'application/json'
              },
              body: JSON.stringify({
                item: id,
              })
            });
            let result =await res.json();
            if(result.success)
            {
                toast.info("Item Removed Successfully",{position: toast.POSITION.BOTTOM_CENTER})
                this.setState({
                    showlist:true
                })
                this.cart();    
            }
        }
        catch(e)
        {
            console.log(e)
        }
    }

    async cart(){
        try{
            let res =await fetch('/cartproducts',{
              method:'post',
              headers:{
                'Accept':'application/json',
                'Content-Type': 'application/json'
              },
            });
            let result =await res.json();
            if(result.success)
            {
                this.setState({                    
                    productlist:result.msg,
                    showlist:true,
                })
                

            }
        }
        catch(e)
        {
            console.log(e)
        }
    }
    render(){
        if(this.state.showlist){
        
            const personList = this.state.productlist.map(person=>(
                <tr>
                    <td class="cart_product_item">
                        <img src={person.Image} alt=""/>
                        <div class="cart_producT_item_title">
                            <h6>{person.Name}</h6>
                            <div class="rating">
                                <i class="fa fa-star"></i>
                                <i class="fa fa-star"></i>
                                <i class="fa fa-star"></i>
                                <i class="fa fa-star"></i>
                                <i class="fa fa-star"></i>
                            </div>
                        </div>
                    </td>

                    <td class="cart_price"> Rs. {person.Prize}</td>
                    <td class="cart_quantity">
                        <input type="number" className={person.id} name="tentacles" min="1" max="100" placeholder='1' onChange={()=>this.updateprize(person.Prize,person.id)}/>
                    </td>                                    
                    <td class="cart_total" id={person.id}></td>
                    <td class="cart_close"><span class="icon_close" onClick={()=>this.deleteproduct(person.id)}><i class="fa fa-close"></i></span></td>
                </tr>
            ))
        return(
            <section class="shop-cart spad">
                <div class="container">
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="shop_cart_table">
                                <table>
                                    <thead>
                                        <tr>
                                            <th>Product</th>
                                            <th>Price</th>
                                            <th>Quantity</th>
                                            <th>Total</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {personList}   
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-lg-6 col-md-6 col-sm-6">
                            <div class="cart_btn"><a href="#">Continue Shopping</a></div>
                        </div>
                        <div class="col-lg-6 col-md-6 col-sm-6">
                            <div class="cart_btn update__btn"><a href="#"><span class="icon_loading"></span> Update cart</a></div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-lg-6">
                            <div class="discount_content">
                                <h6>Discount codes</h6>
                                    <form action="#">
                                        <input type="text" placeholder="Enter your coupon code"/>
                                        <button type="submit" class="site-btn">Apply</button>
                                    </form>
                            </div>
                        </div>
                        <div class="col-lg-4 offset-lg-2">
                            <div class="cart_total_procced">
                                <h6>Cart total</h6>
                                <ul>
                                    <li>Subtotal <span>Rs. {this.state.total}</span></li>
                                    <li>Total <span>Rs. {this.state.total}</span></li>
                                </ul>
                                <a href="#" class="primary-btn">Proceed to checkout</a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
    else{
        return(
            <h1>NO PRODUCTS AVAILABLE</h1>
        )
    }
}
}
export default Cartproduct